import { Head, Link } from '@inertiajs/react'

import cs from './InertiaExample.module.css'

export default function Index({room_names}: { room_names: Array<unknown> | null }) {
  return (
    <>
      <Head title="Inertia + Vite Ruby + React Example" />

      <div className={cs.root}>
        <div>
            <h1 className={cs.h1}>Welcome to ChatApp!</h1>
            <small className='text-[#0000FF] text-sm'>Please fulfil your data and select room before further actions!</small>
        </div>
        <div className='flex flex-col items-center'>
            <div className="block card mt-5 max-w-[32rem] w-[32rem] md:w-[40rem] md:max-w-[40rem]">
                <h2 className="text-2xl font-bold bg-gray-300 rounded-md">Select Room: </h2>
                <div className='mt-5'>
                    {
                        room_names && room_names.length > 0
                        ? room_names.map((room_name: unknown) => 
                            <>
                                <Link href={`/${room_name}`} className="bg-blue-300 text-[16pt] rounded-lg flex w-full justify-center" key={room_name as string}>
                                    {room_name as string}
                                </Link>
                            </>
                        )
                        : <p className='text-[#FF0000] text-sm'>No room available</p>
                    }
                </div>
            </div>
        </div>
      </div>
    </>
  )
}
